:root {
  --width: 1196px;
}
html {
    font-size: 62.5%;
    height: 100%;
    overflow-y: scroll;
    font-family: GothamProRegular, Arial, sans-serif;
}
html, body{
  width: 100%;
  margin:0;
  padding: 0;
}

body{
  font-size: 1.8rem;
  line-height: 1.5;
  min-width: var(--width);
}

section{
  width: 100%;
  padding: 0;
  margin: 0;
  h1{
    font-size: 6rem;
    line-height: 1;
    font-weight: normal;

  }
  strong{
    font-family: GothamProBold, Arial, sans-serif;
    font-weight: normal;
  }
}
