section#intro{
  position: relative;
  background: #303939 url(../img/bg1.jpg) no-repeat;
  background-size: cover;
  height: 1018px;
  color: #fff;
  .intro-wrap{
    top: 355px;
    width: 50%;
    background: rgba(79,127,122, 0.7);
    height: 340px;
    position: relative;

    .intro-content{
      position: absolute;
      right: calc(-var(--width)/2);
      width: var(--width);
      display: flex;
      .intro-video{
        width: 605px;
        flex: 0 1 605px;
      }
      &>header{
        flex: 1;
        h1{
          font-family: Oranienbaum, Times, serif;
          font-size: 6rem;
          line-height: 1;
          font-weight:normal;
        }
        p{
          margin: 0;
        }
      }
    }
  }
  .about-link{
    position: absolute;
    bottom: 94px;
    left: 50%;
    width: 274px;
    margin-left: calc(-274px / 2);
    text-align: center;
    letter-spacing: 3px;
    &:any-link{
      color: #fff;
      text-decoration: none;
    }

    &:before{
      content: ' ';
      transition: 0.2s;
      position: absolute;
      width: 32px;
      height: 18px;
      left: 50%;
      margin-left: calc(-32px / 2);
      background: transparent url(../img/about-link.png) no-repeat;
      bottom: -36px;
    }
    &:hover{
      &:before{
        bottom: -50px;
      }
    }
  }

}
