section#ecology{
  margin: 0 auto;
  width: var(--width);
  background: #fff;
  color: #000;
  h1{
    margin: 224px 0 80px 0;
    font-family: Oranienbaum, Times, serif;
    font-size: 6rem;
    line-height: 1;
    font-weight:normal;
  }
  >ul{
    list-style: none;
    display: flex;
    padding: 0 55px;
    margin:0 0 165px 0;
    width: calc(var(--width) - 55px * 2);
    justify-content: space-between;
    >li{
      flex: 0 1 350px;
      article{
        header{
          margin-bottom: 40px;
          h2{
            text-align: center;
            font-weight: normal;
            font-family: GothamProBold, Arial, sans-serif;
            font-size: 2.4rem;
          }

        }
        p,ul{
          text-align: justify;
          font-size: 1.4rem;
          line-height: 2;
          margin: 0;
          padding: 0;
        }
        ul{
          list-style-position: inside;
          list-style-type: disc;
        }
        li:first-letter{
          padding-left: 20px;
        }
      }

    }
  }

}
