section#compatibility{
  position: relative;
  background: #303939 url(../img/bg4.jpg) no-repeat;
  background-size: cover;
  height: 1075px;
  color: #fff;
  &>header{
        top: 220px;
        width: 50%;
        height: 230px;
        padding-right: 232px;
        position: relative;
        background: rgba(27,29,29, 0.7);
        h1{
          position: absolute;
          right: 0;
          width: 840px;
          font-family: Oranienbaum, Times, serif;
          font-size: 6rem;
          line-height: 1;
          font-weight:normal;
        }

  }
  article{
    margin: 294px auto 0 auto;
    width: var(--width);
    display: flex;
    justify-content:flex-start;
    .info{
      flex: 0 1 500px;
      padding-left: 55px;
    }
    h2{
      text-align: center;
    }
    p{
      text-align: justify;
    }
    a:any-link{
      text-decoration: none;
      text-align: center;
      color: white;
      border-bottom: 1px solid  color(white alpha(-70%));
      transition: 0.4s;
    }
    .links{
      margin-left: 140px;
      padding-top: 90px;
      text-align: center;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      div{
        margin-bottom: 40px;
      }
    }
    a:hover{
      border-bottom: 1px solid  color(white alpha(-100%));
    }
  }


}
