


#header{
  position: fixed;
  z-index: 1000;
  width: 100%;
  height: 113px;
  top:0;
  background: rgba(79,127,122,0.0);
  transition: 0.5s;
  overflow: hidden;
  .header-content{
    position: relative;
    z-index: 1100;
    width: var(--width);
    margin: 0 auto;
    height: 113px;
    align-items: center;
    display: flex;
    justify-content: space-between;

    .social{
      position: relative;
      display: flex;
      flex: 0 1 410px;
      justify-content: space-between;
      a{
        display: inline-block;
        width: 40px;
        height: 40px;
        &:any-link{
          text-decoration: none;
          color: #fff;
        }
      }
      a.instagram{
        background: transparent url(../img/instagram-icon.png);
      }
      a.vk{
        background: transparent url(../img/vk-icon.png);
      }
      a.facebook{
        background: transparent url(../img/facebook-icon.png);
      }
      .header-phone{
        width: 190px;
        flex: 0 1 210px;

        margin-left: 50px;
      }
      a.phone{
        padding-left: 40px;
        width: 160px;
        font-size: 1.4rem;
        line-height: 40px;
        background: transparent url(../img/phone-icon.png) no-repeat left;
      }
    }

    .header-navigation{
      flex: 0 1 370px;
      width: 370px;
      margin-left: auto;
      display: flex;
      justify-content: space-between;
      a{
        display: inline-block;
        &:any-link{
          text-decoration: none;
          color: #fff;

        }
      }
      a.addr{
        height: 42px;
        padding-left: 55px;
        line-height: 42px;
        font-size: 1.4rem;
        background: transparent url(../img/addr-icon.png) no-repeat left;
        span{
          border-bottom: 1px solid color(white alpha(-70%));
        }
      }
      a.header-menu{
        width: 36px;
        height: 36px;
        background: transparent url(../img/menu-icon.png);
      }
    }

  }
  .header-logo{
    z-index: 1050;
    width: 341px;
    height: 113px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    background: transparent url(../img/header-logo.png);
    background-size: cover;
    top:0;
  }
  nav{
    position: relative;
    width: 100%;
    display: block;
    height: 70px;
    background: #fff;
    overflow: hidden;
    z-index: 1070;
    line-height: 70px;
    transition: 0.4s;
    opacity: 0;
    ul{
      margin: 0 auto;;
      padding: 0;
      width: var(--width);
      list-style: none;
      display: flex;
      justify-content: space-between;
      li{
        margin: 0;
        padding: 0;
        a{
          text-transform: uppercase;
          color: #000;
          font-family: GothamProBold, Arial, sans-serif;
          font-weight: normal;
          font-size: 1.4rem;
        }
        a:any-link{
          text-decoration: none;
        }
        a:hover{
          color: #4f7f7a;
        }
      }
    }
  }
}
#header.menu{
  height: calc(113px + 70px);
  nav{
    opacity: 1;
  }
}
#header.small{
  top: -20px;
  height: 100px;
  background: rgba(39,128,123, 1);
  .header-content{
      height: 100px;
  }
}
#header.small.menu{
  height: 170px;
  nav{
    opacity: 1;
  }
}

.intro a.intro,
.tips a.tips,
.beauty a.beauty,
.ecology a.ecology,
.history a.history,
.quality a.quality,
.compatibility a.compatibility,
.interiors a.interiors,
.contacts a.contacts,
.partners a.partners{
  color: #4f7f7a !important;
}
