section#beauty{
  position: relative;
  background: #303939 url(../img/bg2.jpg) no-repeat;
  background-size: cover;
  height: 1075px;
  color: #fff;
  header{
        top: 220px;
        width: 50%;
        height: 230px;
        padding-right: 232px;
        position: relative;
        background: rgba(110,128,72, 0.7);
        h1{
          position: absolute;
          right: 0;
          width: 840px;
          font-family: Oranienbaum, Times, serif;
          font-size: 6rem;
          line-height: 1;
          font-weight:normal;
        }

  }
  &>ul{
    padding: 0 55px;
    margin: 280px auto 0 auto;
    width: calc(var(--width) - 55px * 2);
    list-style: none;
    display: flex;
    justify-content: space-between;
    li{
      flex: 0 1 500px;
    }

    h2{
      margin: 0;
      text-align: center;;
    }
    article{
      text-align: center;
      p{
        text-align: justify;
      }
      a:any-link{
        text-decoration: none;
        color: #fff;
        border-bottom: 1px solid  color(white alpha(-70%));
        transition: 0.4s;
      }
      a:hover{
        border-bottom: 1px solid  color(white alpha(-100%));
      }
    }

  }


}
