section#tips{
  position: relative;
  background: #fff;
  height: 850px;
  color: #000;


  ol{
    padding: 0;
    margin: 227px auto 0 auto;
    width: var(--width);
    column-count: 2;
    list-style: none;
    li{
      margin: 0 0 80px 0;
      position: relative;
      &:after{
        content: attr(title);
        color: #fff;
        text-align: center;
        font-family: Oranienbaum, Times, serif;
        line-height: 34px;
        left: 0;
        top: 0;
        position: absolute;
        width: 29px;
        height: 38px;
        background: transparent url(../img/ol.png);

      }
      article{
        padding-left: 55px;
      }
      h2{
        line-height: 1;
        margin: 0;
        padding: 0;
        font-family: Oranienbaum, Times, serif;
        font-weight: normal;
        font-size: 3.6rem;
      }
    }
  }
}
