section#partners{
  position: relative;
  background: #303939 url(../img/bg5.jpg) no-repeat;
  background-size: cover;
  height: 950px;
  color: #fff;

        h1{
          top: 220px;
          position: relative;
          width: var(--width);
          margin: 0 auto;
          font-family: Oranienbaum, Times, serif;
          font-size: 6rem;
          line-height: 1;
          font-weight:normal;
        }
        .footer-info{
          width: var(--width);
          line-height: 2;
          margin: 250px auto 0 auto;
          .footer-button{
            display: inline-block;
            text-transform: uppercase;
            background: #cd9f4c;
            font-family: GothamProBold, Arial, sans-serif;
            padding: 15px 45px;
            line-height: 1;
            &:any-link{
              color: #fff;
              text-decoration: none;

            }
          }

        }
        .footer-partners{
              margin-top: 100px;
              width: 50%;
              height: 85px;
              padding-right: calc(var(--width)/2 - 55px);
              position: relative;
              background: rgba(135,107,57, 0.8);
              .partners-content{
                text-transform: uppercase;
                position: absolute;
                right: 0;
                width: 1143px;
                display: flex;
                line-height: 85px;
                justify-content: space-around;
                img{
                  display: inline-block;
                  align-self: center;
                }
              }

        }
        .footer-logo{
          width: 341px;
          height: 143px;
          position: absolute;
          bottom: 0;
          left: 50%;
          margin-left: calc(-341px / 2);
          background: transparent url(../img/footer-logo.png);
        }
}
