section#history{
  position: relative;
  background: #303939 url(../img/bg3.jpg) no-repeat;
  background-size: cover;
  height: 1075px;
  color: #fff;
  &>header{
        top: 220px;
        width: 50%;
        height: 230px;
        padding-right: 232px;
        position: relative;
        background: rgba(149,61,47, 0.7);
        h1{
          position: absolute;
          right: 0;
          width: 840px;
          font-family: Oranienbaum, Times, serif;
          font-size: 6rem;
          line-height: 1;
          font-weight:normal;
        }

  }
  article{
    margin: 294px auto 0 auto;
    width: var(--width);
    display: flex;
    justify-content: space-between;
    background: rgba(149,61,47, 0.7);
    h2, p{
      padding: 30px 55px 65px 55px;
      font-weight: normal;
      font-size: 1.8rem;

    }
    p{
      text-align: justify;
    }
    header,p{
      display: inline-block;
      flex: 0 1 550px;
    }

  }


}
