section#interiors{
  margin: 0 auto;
  width: var(--width);
  background: #fff;
  color: #000;
  h1{
    margin: 224px 0 80px 0;
    font-family: Oranienbaum, Times, serif;
    font-size: 6rem;
    line-height: 1;
    font-weight:normal;
  }
  .photo{
    display: block;
    width: 100%;
    margin-bottom: 200px;
    span{
      border: 0 !important;
      box-shadow: none !important;
    }
    &>span{
      &>span:first-child{
        display:none;
      }
    }
  }
  .links{
    display: flex;
    margin: 108px 0 196px 0;
    padding: 0 200px;
    justify-content: space-around;
    div{
      text-align: center;
    }

    a:any-link{
      text-decoration: none;
      text-align: center;
      color: black;
      border-bottom: 1px solid  color(black alpha(-70%));
      transition: 0.4s;
    }
    a:hover{
      border-bottom: 1px solid  color(black alpha(-100%));
    }
  }
}
