section#contacts{
  /*
  margin: 0 auto;
  width: var(--width);
  background: #fff;
  position: relative;
  */
  width: 100%;
  color: #000;
  height: 680px;
  position: relative;
  h1{
    z-index: 999;
    position: absolute;
    left: 50%;
    padding: 0;
    margin: 0;
    margin-left: calc(-var(--width) / 2);
    top: 180px;
    font-family: Oranienbaum, Times, serif;
    font-size: 6rem;
    line-height: 1;
    font-weight:normal;
  }

  .links{
    display: flex;
    margin: 108px 0 196px 0;
    padding: 0 200px;
    justify-content: space-around;
    div{
      text-align: center;
    }

    a:any-link{
      text-decoration: none;
      text-align: center;
      color: black;
      border-bottom: 1px solid  color(black alpha(-70%));
      transition: 0.4s;
    }
    a:hover{
      border-bottom: 1px solid  color(black alpha(-100%));
    }
  }
}
.map-container{
  width: 100%;
  height: 680px;
  position: relative;
  #addr-map{
    z-index: 900;
    width: 100%;
    height: 100%;
  }
}
