section#quality{
  margin: 0 auto;
  width: var(--width);
  background: #fff;
  color: #000;
  h1{
    margin: 224px 0 80px 0;
    font-family: Oranienbaum, Times, serif;
    font-size: 6rem;
    line-height: 1;
    font-weight:normal;
  }
  >ol{
    list-style: none;
    display: flex;
    justify-content: space-between;
    padding: 0;
    margin: 0;
    li{
      padding: 0;
      margin: 0;
      text-align: center;
      flex: 1;
      position: relative;
      padding-top: 100px;
      &:after{
        content: attr(title);
        position: absolute;
        left: 50%;
        margin-left: calc(-58px / 2);
        width: 58px;
        height: 76px;
        top: 0;
        line-height: 70px;
        color: white;
        font-family: Oranienbaum, Times, serif;
        font-size: 4rem;
        background: transparent url(../img/olq.png) no-repeat;
      }
      h2{
        font-weight: normal;
        font-size: 2.4rem;
        font-family: GothamProBold, Arial, sans-serif;
        line-height: 1.2;
      }
    }
  }
  article{
    display: flex;
    flex-direction: column;
    margin-top: 90px;
    .info{
      display: flex;
      justify-content: space-between;
      p{
        display: inline-block;
        flex: 0 0 410px;

      }
      .images{
        width: 702px;
        flex: 0 0 702px;
        div{
          width: 160px;
          padding: 20px;
        }
        img, div{
          display: inline-block;
          float: left;
        }
      }
    }
    .links{
      display: flex;
      margin: 108px 0 196px 0;
      padding: 0 200px;
      justify-content: space-around;
      div{
        text-align: center;
      }

      a:any-link{
        text-decoration: none;
        text-align: center;
        color: black;
        border-bottom: 1px solid  color(black alpha(-70%));
        transition: 0.4s;
      }
      a:hover{
        border-bottom: 1px solid  color(black alpha(-100%));
      }
    }
  }

}
